<template>
  <div>
    <!-- <button
      type="button"
      class="btn btn-primary"
      data-toggle="modal"
      data-target="#exampleModal"
    >
      modal btn
    </button> -->
    <div class="btn" data-toggle="modal" data-target="#exampleModal">
      modal btn
    </div>

    <ModalWindow
      text="あなたの先延ばし傾向をチェックしてみましょう"
      show-btn-flg="0"
    />

    <b-container fluid="md">
      <!-- calendar -->
      <VCalendar :attributes="dates" />

      <div class="card p-2">
        スマホを引き出しにしまいましたか？<br />作業をするのに完璧な環境を整えましょう！
      </div>
      <h3>未達成タスク</h3>
      <ul class="card">
        <li
          v-for="(task, index) in doingTasks"
          :key="index"
          class="card m-2 p-2"
        >
          <div>
            <BFormCheckbox v-model="task.finished" value="1" class="pb-2">
              <div :class="{ finished: task.finished == 1 }" class="pb-2">
                {{ task.name }}
              </div>
            </BFormCheckbox>
            <BListGroup>
              <BListGroupItem
                v-for="(step, index) in task.steps"
                :key="index"
                class="p-2"
              >
                <BFormCheckbox
                  v-model="step.finished"
                  value="1"
                  class="b-block"
                >
                  <span
                    :class="{
                      finished: step.finished == 1 || task.finished == 1,
                    }"
                  >
                    {{ step.step }}
                  </span>
                </BFormCheckbox>
              </BListGroupItem>
            </BListGroup>
          </div>
        </li>
      </ul>
      <h3>完了済みのタスク</h3>
      <ul>
        <li v-for="(task, index) in finishTasks" :key="index" class="card">
          <BFormCheckbox v-model="task.finished" value="1">
            <span class="finished">{{ task.name }}</span>
          </BFormCheckbox>
        </li>
      </ul>
      <h3>全てのタスクとステップ表示</h3>
      <ul>
        <li v-for="(task, index) in tasks" :key="index" class="card p-2">
          <BFormCheckbox v-model="task.finished" value="1">
            <span class="pb-2" :class="{ finished: task.finished == 1 }">
              {{ task.name }}
            </span>
          </BFormCheckbox>
          <BListGroup>
            <BListGroupItem
              v-for="(step, index) in task.steps"
              :key="index"
              class="p-2"
            >
              <BFormCheckbox v-model="step.finished" value="1" class="b-block">
                <span
                  :class="{
                    finished: step.finished == 1 || task.finished == 1,
                  }"
                >
                  {{ step.step }}
                </span>
              </BFormCheckbox>
            </BListGroupItem>
          </BListGroup>
        </li>
      </ul>
    </b-container>
  </div>
</template>

<script>
import store from "@/store";
import ModalWindow from "@/components/ModalWindow.vue";
import { ref } from "vue";

export default {
  data() {
    return {
      tasks: [],
      steps: [],
      today: "",
      todayTasks: [],
      dates: ref([
        {
          // Boolean
          bar: true,
          dates: [
            new Date(2023, 10, 1),
            new Date(2023, 10, 10),
            new Date(2023, 10, 22),
          ],
          popover: {
            label: "Take Noah to basketball practice.",
          },
          isComplete: false,
        },
        {
          // String
          bar: "red",
          dates: [
            new Date(2023, 10, 4),
            new Date(2023, 10, 10),
            new Date(2023, 10, 15),
          ],
        },
        {
          // Object
          bar: {
            style: {
              backgroundColor: "brown",
            },
          },
          dates: [
            new Date(2023, 10, 12),
            new Date(2023, 10, 26),
            new Date(2023, 10, 15),
          ],
        },
      ]),
    };
  },
  components: {
    ModalWindow,
  },
  mounted() {
    this.tasks = store.getters.getTasks;
    this.steps = store.getters.getSteps;

    // extraction today's task
    this.today = new Date();

    this.tasks.forEach((task) => {
      if (
        new Date(task.start_date) <= this.today &&
        new Date(task.end_date) >= this.today
      ) {
        this.todayTasks.push(task);
      }
    });
  },
  computed: {
    // 作業中タスクの取得
    doingTasks() {
      return this.tasks.filter((task) => !task.finished);
    },

    // 完了済みタスクの取得
    finishTasks() {
      return this.tasks.filter((task) => task.finished == 1);
    },
    // カレンダーの属性
    // attributes() {
    //   const calendarDates = this.dates;
    //   return;
    // },
  },
  watch: {
    tasks: {
      handler: function () {
        store.dispatch("changeTask", { task: this.tasks });
      },
      deep: true,
    },
  },
  methods: {
    finishToDo(index) {
      this.tasks.splice(index, 1);
    },
  },
};
</script>

<style>
.finished {
  color: gray;
  text-decoration: line-through;
}
</style>
