<template>
  <div>
    <Pie :data="data" :options="options" />
    {{ labels }}
  </div>
</template>

<script>
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";
import { Pie } from "vue-chartjs";

ChartJS.register(ArcElement, Tooltip, Legend);

export default {
  props: ["labels", "datas"],
  components: {
    Pie,
  },
  data() {
    return {
      data: {
        labels: this.labels,
        datasets: [
          {
            backgroundColor: [
              "#41B883",
              "#E46651",
              "#00D8FF",
              "#DD1B16",
              "#ccc",
            ],
            data: this.datas,
          },
        ],
      },
      options: {
        responsive: true,
        // maintainAspectRatio: true,
      },
    };
  },
};
</script>
