<template>
  <b-container fluid="md">
    <h2 class="m-0">タスク作成</h2>
    <b-form>
      <!-- タスク名入力 -->
      <b-form-group class="mt-1">
        <label for="task-name">タスク名</label>
        <BFormInput
          id="task-name"
          v-model="taskName"
          placeholder="タスク名を入力"
          required
          autofocus
        />
      </b-form-group>

      <!-- カテゴリ選択 -->
      <!-- <b-form-group class="mt-3">
        <label for="task-category">優先順位</label>
        <BFormSelect
          class="form-control"
          :options="categoryOptions"
          v-model="category"
          required
        />
      </b-form-group> -->

      <!-- ステップ追加 -->
      <b-form-group class="mt-3" label="ステップ">
        <b-input-group class="mb-2" v-for="(step, index) in steps" :key="index">
          <BFormInput type="text" v-model="steps[index]" />
          <b-input-group-append v-if="steps.length > 1">
            <b-button variant="secondary" @click="removeStep(index)"
              >削除</b-button
            >
          </b-input-group-append>
        </b-input-group>
        <b-button variant="secondary" @click="addStep" class="mx-auto"
          >ステップを追加＋</b-button
        >
      </b-form-group>

      <!-- 目安作業時間入力 -->
      <b-form-group class="mt-3" label="作業時間の目安">
        <b-input-group append="分">
          <BFormInput type="number" v-model="guidelineStepTime" />
        </b-input-group>
      </b-form-group>

      <!-- 期限選択 -->
      <b-form-group class="mt-3" label="期限">
        <BFormInput
          type="date"
          class="form-control"
          v-model="deadLineDate"
          :min="today"
          @input="selectDeadlineDate()"
        />
        <BFormInput
          type="time"
          class="form-control"
          v-if="addTimeFlg"
          v-model="deadLineTime"
        />
        <BFormCheckbox
          id="addTimeCheck"
          class="form-check-input"
          v-model="addTimeFlg"
        >
          時間を追加
        </BFormCheckbox>
      </b-form-group>

      <!-- 取り組み期間選択 -->
      <b-form-group class="mt-3" label="スケジュール">
        <!-- {{ range }} -->
        <VDatePicker
          expanded
          mode="date"
          v-model.range="range"
          :min-date="today"
        />
        <b-row>
          <b-col>
            <BFormInput type="date" class="form-control" v-model="startDate" />
          </b-col>
          ～
          <b-col>
            <BFormInput type="date" class="form-control" v-model="endDate" />
          </b-col>
        </b-row>
      </b-form-group>
    </b-form>
    <b-button block variant="primary" @click="createTask">
      タスクを登録
    </b-button>
  </b-container>
</template>

<script>
import router from "@/router";
import store from "@/store";
import { ref } from "vue";
import { formingDate } from "@/script/common.js";
// import { datePartIsValid } from "v-calendar/dist/types/src/utils/date/helpers";

export default {
  data() {
    return {
      range: ref({}),
      today: null,
      taskName: "レポート",
      categoryOptions: [
        { value: null, text: "未分類" },
        { value: 1, text: "課題" },
        { value: 2, text: "生活" },
        { value: 3, text: "その他" },
      ],
      category: null,
      steps: ["テーマ選択", "文献調査", "下書き作成", "清書", "見直し", "提出"],
      addTimeFlg: false,
      guidelineStepTime: 10,
      deadLineDate: null,
      deadLineTime: "12:00:00",
      startDate: null,
      endDate: null,
    };
  },
  mounted() {
    this.today = formingDate(new Date());
    this.deadLineDate = new Date();
  },
  watch: {
    range: {
      handler: function (val) {
        this.startDate = formingDate(val.start);
        this.endDate = formingDate(val.end);
      },
      deep: true,
    },
    startDate(val) {
      const start = new Date(val);
      if (start !== this.range.start) {
        this.range.start = start;
        console.log(this.range.start);
      }
    },
  },
  methods: {
    // add new step
    addStep() {
      this.steps.push("");
    },
    // remove step
    removeStep(index) {
      this.steps.splice(index, 1);
    },
    selectDeadlineDate() {
      // const endDate = this.deadLineDate;
      // this.range.end = endDate;
    },
    // create new task
    createTask() {
      const taskJson = {
        name: this.taskName,
        category: this.category,
        guideline_step_time: this.guidelineStepTime,
        deadline_date: this.deadLineDate,
        deadline_time: this.deadLineTime,
        start_date: this.startDate,
        end_date: this.endDate,
        finished: 0,
        steps: [],
      };

      const taskStep = [];
      this.steps.forEach((step) => {
        taskStep.push({
          step,
          finished: 0,
        });
      });
      console.log(taskStep);

      let id = 0;
      if (store.getters.getTasks.length < 1) {
        id = 1;
      } else {
        id = store.getters.getTasks.slice(-1)[0].id + 1;
      }

      taskJson.id = id;
      taskJson.steps = taskStep;

      if (this.addTimeFlg === false) {
        taskJson.deadline_time = null;
      }

      // save task data
      store.dispatch("addTask", { task: taskJson });

      // move list page
      router.push("/list");
    },
  },
};
</script>

